<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-Flex对齐1">Flex对齐</a></li><li><a href="#vue-doc-栅格顺序Flex1">栅格顺序(Flex)</a></li><li><a href="#vue-doc-区块间隔1">区块间隔</a></li><li><a href="#vue-doc-Flex布局1">Flex布局</a></li><li><a href="#vue-doc-左右偏移1">左右偏移</a></li><li><a href="#vue-doc-栅格排序1">栅格排序</a></li><li><a href="#vue-doc-响应式布局1">响应式布局</a></li><li><a href="#vue-doc-其它属性的响应式1">其它属性的响应式</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-grid栅格1" class="vue-doc-element">Grid 栅格</h1>
<p class="vue-doc-element">我们采用了24栅格系统，将区域进行24等分，这样可以轻松应对大部分布局问题。使用栅格系统进行网页布局，可以使页面排版美观、舒适。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-grid1" class="vue-doc-element">Grid</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

