        <div class="vue-doc-components">
          <demobasic></demobasic>
<demoalign></demoalign>
<demoflex></demoflex>
<demogutter></demogutter>
<demojustify></demojustify>
<demooffset></demooffset>
<demopush_pull></demopush_pull>
<demoxs_sm_md_lg></demoxs_sm_md_lg>
<demoother></demoother>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-rowprops1_1" class="vue-doc-element">Row props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th style="text-align:left">属性</th>
<th style="text-align:left">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">gutter</td>
<td style="text-align:left">栅格间距，单位 px，左右平分</td>
<td style="text-align:center">Number</td>
<td style="text-align:center">0</td>
</tr>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">布局模式，可选值为flex或不选，在现代浏览器下有效</td>
<td style="text-align:center">String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">align</td>
<td style="text-align:left">flex 布局下的垂直对齐方式，可选值为top、middle、bottom</td>
<td style="text-align:center">String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">justify</td>
<td style="text-align:left">flex 布局下的水平排列方式，可选值为start、end、center、space-around、space-between</td>
<td style="text-align:center">String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">class-name</td>
<td style="text-align:left">自定义的class名称</td>
<td style="text-align:center">String</td>
<td style="text-align:center">-</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-colprops1_2" class="vue-doc-element">Col props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th style="text-align:left">属性</th>
<th style="text-align:left">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">span</td>
<td style="text-align:left">栅格的占位格数，可选值为0~24的整数，为 0 时，相当于display:none</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">order</td>
<td style="text-align:left">栅格的顺序，在flex布局模式下有效</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">offset</td>
<td style="text-align:left">栅格左侧的间隔格数，间隔内不可以有栅格</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">push</td>
<td style="text-align:left">栅格向右移动格数</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">pull</td>
<td style="text-align:left">栅格向左移动格数</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">class-name</td>
<td style="text-align:left">自定义的class名称</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">xs</td>
<td style="text-align:left">&lt;768px 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">sm</td>
<td style="text-align:left">≥768px 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">md</td>
<td style="text-align:left">≥992px 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">lg</td>
<td style="text-align:left">≥1200px 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td style="text-align:center">Number / String</td>
<td style="text-align:center">-</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th style="text-align:left">Attribute</th>
<th style="text-align:left">Explanation</th>
<th style="text-align:center">Type</th>
<th style="text-align:center">Default</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td style="text-align:center">String</td>
<td style="text-align:center">-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demoalign from './demo/align.md';
import demoflex from './demo/flex.md';
import demogutter from './demo/gutter.md';
import demojustify from './demo/justify.md';
import demooffset from './demo/offset.md';
import demopush_pull from './demo/push_pull.md';
import demoxs_sm_md_lg from './demo/xs_sm_md_lg.md';
import demoother from './demo/other.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demoalign,demoflex,demogutter,demojustify,demooffset,demopush_pull,demoxs_sm_md_lg,demoother
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


